import * as React from 'react';
import { Controller, environment } from '@ali/4ever-cangjie';
import { MenuItem } from '@ali/we-design';
import { MenuItemInnerProps } from '@ali/4ever-component';
import { clearStyles } from '../actions';

const MOD = environment.IS_MAC ? 'âŒ˜' : 'Ctrl';

export const CLEAR_STYLE_HOT_KEY = `${MOD}+\\`;

export default function ClearStyleMenu(props: MenuItemInnerProps) {
  const { menuItem, controller, locale } = props;
  const { title, ...rest } = menuItem;
  const handleClick = React.useCallback(() => {
    controller.run('onAction', clearStyles());
  }, [controller]);

  return (
    <MenuItem
      {...rest}
      onClick={handleClick}
      testid={'bi-toolbar-clearstyle'}
    >
      {title || locale?.buttonTitle}
    </MenuItem>
  );
}
